    border-radius: 50%;
    border: none;
    margin-top: 23px;
    margin-left: 28px;
}

@media (max-width:767.9px) {
    .modul-feedback__opened {
        z-index: 3;
        background-color: rgba(255, 255, 255, 1);
        width: 100%;
        height: 100%;
        padding: 0;
        margin-top: 0px;
        position: absolute;
    }

    .modul-call__opened {
        z-index: 3;
        background-color: rgba(255, 255, 255, 1);
