.services-menu {
    width: 100%;
    margin: 0px;
    padding: 0px;
}

.services-menu__header {
    margin-top: 32px;
    padding: 16px;
    border-top: 1px solid rgba(217, 255, 245, 1);
    vertical-align: middle;
    background-image: linear-gradient(90deg, #F8F8F8 1.39%, #FFFFFF 100%);
}

.services-menu__header h1 {
    margin: 0;
    font-family: 'TT Lakes';
    text-transform: uppercase;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    color: rgba(126, 126, 130, 1);
    font-weight: 200;
}

.services-menu {
    overflow: auto;
    white-space: nowrap;
}

.services-menu::-webkit-scrollbar {
    width: 0;
}

.brand_logo {
    width: 108px;
    height: 32px;
    position: absolute;
    left: 16px;
    top: 20px;
}

.goBtn_logo {
    width: 40px;
    height: 40px;
    position: absolute;
    top: 16px;
    right: 16px;
}

.button {
    display: flex;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    background-color: transparent;
    border: 0;
    font-weight: 500;
    color: rgba(27, 28, 33, 1);
    margin-top: 24px; 
}

.button::before {
    content: "";
    width: 24px;
    height: 24px;
    display: block;
    background-image: url(./img/expand_logo.svg);  
}

.button:hover::before {
    animation-name: expandButton;
    animation-duration: 1s; 
    animation-iteration-count: 1;
    animation-fill-mode: forwards;
}

.button:hover {
    text-decoration: underline;
}

.button1 {
    display: flex;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    background-color: transparent;
    border: 0;
    font-weight: 500;
    color: rgba(27, 28, 33, 1);
    margin-top: 24px; 
}

.button1::before {
    content: "";
    width: 24px;
    height: 24px;
    display: block;
    background-image: url(./img/expand_logo.svg);
    transform: rotate(180deg);
}

.button1:hover::before {
    animation-name: expandButton1;
    animation-duration: 1s; 
    animation-fill-mode: forwards;
    animation-iteration-count: 1;
}

.button1:hover {
    text-decoration: underline;
}

.extra-item768 {
    display: none;
}

.extra-item1120 {
    display: none;
}

.extra-item__visiable {
    display: inline-block;
}

.extra-item__hidden {
    display: none;
}

.swiper {
    width: 100%;
    height: 130px;
  }

.swiper-slide {
    background-color: (234, 234, 234, 1);
    width: 240px;
    height: 72px;
    border-radius: 5px;
    border: 1px solid rgba(234, 234, 234, 1);
    margin: auto;
    margin-top: 16px;
    align-items: center;
    position: relative;
    display: inline-block;
    color: #7E7E82;
    text-align: center;
    text-decoration: none;
    margin-left: 16px;
}

.brand_select {
    background-color: (234, 234, 234, 1);
    width: 240px;
    height: 72px;
    border-radius: 5px;
    border: 1px solid rgba(234, 234, 234, 1);
    margin: auto;
    margin-top: 16px;
    align-items: center;
    position: relative;
    display: inline-block;
    color: #7E7E82;
    text-align: center;
    text-decoration: none;
    margin-left: 16px;
}

.brand_select1 {
    background-color: (234, 234, 234, 1);
    width: 240px;
    height: 72px;
    border-radius: 5px;
    border: 1px solid rgba(234, 234, 234, 1);
    margin: auto;
    margin-top: 16px;
    align-items: center;
    position: relative;
    display: inline-block;
    color: #7E7E82;
    text-align: center;
    text-decoration: none;
    margin-left: 16px;
}

/* -------Services menu + extra items + expand button END-------*/

/* Prices menu and offer */
.prices-menu__slide {
    height: 200px;
}

.swiper__prices {
    height: 260px;
}

.prices-menu__slide__info {
    font-size: 14px;
    line-height: 18px;
    margin-left: 10px;
    text-align: left;
}

.prices-menu__slide__info__title {
    line-height: 16px;
    padding: 0;
    margin-bottom: 0;
}

.prices-menu__slide__info__aside {
    color: black;
    width: 300;
    margin-top: 5px;
}

.order_button {
    position: absolute;
    right: 10px;
    bottom: 10px;
}

.prices-menu__aside {
    display: block;
    margin: 16px;
    font-size: 14px;
    line-height: 20px;
    letter-spacing: 0.2px;
    color: #1B1C21;
    width: 200;
}

.prices-menu__offer {
    display: inline-block;
    margin: 16px 16px 16px 16px;
    font-size: 16px;
    line-height: 14px;
}

.prices-menu__offer::after {
    content: "";
    margin-left: 5px;
    width: 24px;
    height: 24px;
    display: inline-block;
    background-repeat: no-repeat;
    background-position: 0 12px;
    background-image: url(./img/offer_button.svg)
}
/* -----Prices menu and offer end_----- */


/* Repair menu */
.repair-menu__slide {
    height: 160px;
}

.swiper__repair {
    height: 220px;
}

.repair-menu__slide__header {
    position: absolute;
    font-size: 16px;
    line-height: 24px;
    letter-spacing: -0.2px;
    left: 10px;
    top: 10px;
}

.repair-menu__slide__goBtn_logo {
    position: absolute;
    right: 10px;
    bottom: 10px;
}
/* _-----Repair menu END----- */


.footer {
    text-align: center;
    font-size: 14px;
    line-height: 16px;
    color: #7E7E82;
    margin-top: 10px;
    border-top: 1px solid #D9FFF5;
    background-color: #F8F8F8;
}
/*-------END Main------*/

@media (max-width:767.9px) {

}

@media (min-width:768px) and (max-width: 1439.9px) {
    .services-menu {
        overflow: auto;
        white-space: normal;
    }

    .services-menu__header h1{
        width: calc(100% - 48px);
        margin: auto;
        max-width: calc(768px - 48px);
    }

    .services-menu__desktop {
        width: calc(100% - 48px);
        margin: auto;
        max-width: 770px;
    }

    .repair-menu__desktop {
        width: calc(100% - 48px);
        margin: auto;
        max-width: 768px;
    }
 
    .brand_select {
        width: 224px;
        height: 72px;
    }

    .brand_select1 {
        width: 224px;
        height: 72px;
    }

    .swiper {
        display: none;
    }
    
    .brand_select:nth-child(n+7) {
        display: none;
    }

    .repair-menu__slide {
        width: 224px;
        height: 160px;
    }

    .repair-menu__slide1 {
        width: 224px;
        height: 160px;
    }

    .repair-menu__slide:nth-child(n+4) {
        display: none;
    }

    .order_button__desktop {
        width: 119px;
        height: 32px;
    }

    .prices-menu__desktop {
        margin: auto;
    }

    .prices-menu__aside {
        display: block;
        width: 421px;
        height: auto;
        margin-left: 24px;
    }

    .prices-menu__offer {
        margin-left: 24px;
    }

    .footer {
        display: flex;
        flex-direction: row;
        padding-bottom: 24px;
        padding-top: 24px;
    }

    .footer p {
        text-align: left;
        margin-left: 24px;
    }

    .footer p:last-child {
        margin-right: 24px;
        text-align: right;
    }
}

@media (min-width:1440px) {
    .services-menu {
        overflow: auto;
        white-space: normal;
    }

    .services-menu__header h1{
        margin: auto;
        width: 1120px;
    }

    .services-menu__desktop {
        margin: auto;
        width: 1120px;
    }

    .repair-menu__desktop {
        margin: auto;
        max-width: 1120px;
    }

    .brand_select {
        width: 240px;
        height: 72px;
    }

    .brand_select1 {
        width: 240px;
        height: 72px;
    }

    .repair-menu__slide:nth-child(n+5) {
        display: none;
    }

    .repair-menu__slide {
        width: 240px;
        height: 160px;
    }

    .repair-menu__slide1 {
        width: 240px;
        height: 160px;
    }

    .swiper {
        display: none;
    }

    .brand_select:nth-child(n+9) {
        display: none;
    }

    .prices-menu__desktop {
        margin: auto;
    }
